@import 'reset.scss';
@import 'media.scss';
@import 'fonts';
@import 'mixins';

* {
  margin: 0;
  padding: 0;
  //font-size: 1rem;
}

html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: 'Open Sans', sans-serif;
  //font-family: 'Noto Serif JP', serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-size: 1rem;
  // position: fixed;
}

.container {
  max-width: 100%;
  min-width: 320px;
}
.content {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

.wrap {
  min-width: 320px;
  max-width: 1148px;
  //padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
}

.header {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-content: center;
}



